.nav{
    position: fixed;
    top:0;
    width: 100%;
    display: flex;
    padding: 20px;
    z-index: 2;

    transition-timing-function:ease-in ;
    transition: all 0.5s;
}

.nav__black{
    background-color: #0D2325;
}

.nav__logo{
    width: 150px;
    object-fit: contain;
}